import {useEffect, useState} from 'react';
import ColorNick from './user/ColorNick'
import { socket } from '../../store'

import $ from "jquery"

function Chatbox() {
    let [content,setContent] = useState('')
    let [list,setList] = useState([])

    useEffect(() => {
        $.get('/api/chat',data => setList(data))
    },[])
    socket.on('chat', mes => {
        setList(mes)
    })

    const click = () => {
        socket.emit('chat',{
            user: localStorage.login,
            content
        },data => {
            if(data.error) alert(data.message)
            else {
                setContent("")
                $(".chat ul").scrollTop(0)
            }
        })
        
    }
    return (
        <div className='chat'>
            <div className='menu-list'>
                <div className='menu-list_title'>Phòng Chat</div>
                <ul className='menu-list_sub'>
                {list.map((v,k) => (
                    
                    <li key={v.id} className='item'>
                        <span className='user'><ColorNick {...JSON.parse(v.user)} /></span>:
                        <span className='content'>{v.content}</span>
                    </li>
                ))}
                </ul>
                <div className='form'>
                    <input placeholder='Nhập nội dung ...' onKeyDown={e => {
                    <button onClick={click}><i class="fas fa-paper-plane"></i></button>
                    if (e.key === 'Enter') {
                        click()
                    }
                    }} onChange={e => setContent(e.target.value)} value={content} />
                    <button onClick={click}><i className="fas fa-paper-plane"></i></button>
                </div>
            </div>
        </div>
    );
}



export default Chatbox