import React, { useState } from 'react';
import $ from 'jquery'

function Signup(props) {
    let [is,setIs] = useState(true)

    const click = () => setIs(!is)

    return (
        <div className='sign-up'>
           {is ? <Login handelClick={click} /> : <Register handelClick={click} /> }
        </div>
    );
}

function Login({handelClick}){
    let [user,setUser] = useState('')
    let [pass,setPass] = useState('')
  
    const click = () => {
        $.post('/api/login',{
            user,pass
        },data => {
            if(data.error) alert(data.message)
            else {
                alert(data.message)
                localStorage.login = user
                window.location.reload()
            }
        })
    }
    return (
        <div className='sign'>
            <div className='title'>Đăng nhập</div>
            <div className='form'>
                <input type='text' placeholder='Tên đăng nhập ...' onChange={e => setUser(e.target.value)} />
                <input type='text' placeholder='Mật khẩu ...' onChange={e => setPass(e.target.value)} />
                <button onClick={click}>Đăng nhập</button>
                <div onClick={handelClick} style={{padding:'10px',alignText:'center'}}>Bạn chưa có tài khoản ??</div>
            </div>
        </div>
    )
}


function Register({handelClick}){
    let [user,setUser] = useState('')
    let [pass,setPass] = useState('')
    let [rpass,setRpass] = useState('')

    const click = () => {
        $.post('/api/register',{
            user,pass,rpass
        },data => {
            if(data.error) alert(data.message)
            else {
                alert(data.message)
                localStorage.login = user
                window.location.reload()
            }
        })
    }
    return (
        <div className='sign'>
            <div className='title'>Đăng ký</div>
            <div className='form'>
                <input type='text' placeholder='Tên đăng nhập ...' onChange={e => setUser(e.target.value)} />
                <input type='text' placeholder='Mật khẩu ...' onChange={e => setPass(e.target.value)} />
                <input type='text' placeholder='Nhập lại mật khẩu ...' onChange={e => setRpass(e.target.value)} />
                <button onClick={click}>Đăng ký</button>
                <div onClick={handelClick} style={{padding:'10px',alignText:'center'}}>Bạn đã có tài khoản ??</div>
            </div>
        </div>
    )
}

export default Signup