import React from 'react';

const Header = () => {
    return (
        <div className='header'>
            <div className='left'>
                <div className='menu'><i class="fal fa-bars"></i></div>
               <div className='logo'>Poker</div>
            </div>
            <div className='right'>
                <div className='search'><i class="far fa-search"></i></div>
                <div className='mail'><i class="far fa-envelope"></i></div>
                <div className='nofication'><i class="far fa-bell"></i></div>
                
            </div>
        </div>
    );
};

export default Header;